import React, { Component, PropTypes as T } from 'react'

class ProjectDetail extends Component {
  static propTypes = {
    pid: T.string
  }

  contructor (props, context) {
    super(props, context)
  }

  render () {
    return (
      <div></div>
    )
  }
}

export default ProjectDetail



